const $siteList = $('.siteList')
const $last = $('.last')
const $search = $('.search')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)
const hashMap = xObject || [
    {logo: 'C', url: 'https://css-tricks.com/'},
    {logo: 'D', url: 'https://developer.mozilla.org/zh-CN/docs/Web'},
    {logo: 'D', url: 'https://dribbble.com/'},
    {logo: 'G', url: 'https://github.com/'},
    {logo: 'I', url: 'https://www.iconfont.cn/'},
    {logo: 'M', url: 'https://medium.com/'},
    {logo: 'R', url: 'http://www.ruanyifeng.com/blog/'},
    {logo: 'Z', url: 'https://zhengyuan.xyz/'}
]
const simplifyUrl = (url) => {
    return url.replace('http://', '')
        .replace('https://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')//åˆ é™¤/å¼€å¤´çš„å†…å®¹
}


const render = () => {
    $siteList.find('li:not(.last)').remove()
    hashMap.forEach((node, index) => {
        const $li = $(`<li>
        <div class="logoWrap">
            <div class="logo">${node.logo}</div>
        </div>
        <div class="link">${simplifyUrl(node.url)}</div>
        <div class="close">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-close"></use>
            </svg>
        </div>
</li>`)

        $li.insertBefore($last)
        $li.on('click', () => {
            window.open(node.url)
        })
        $li.on('click', '.close', (e) => {
            e.stopPropagation()
            hashMap.splice(index, 1)
            render()
        })
    })
}
render()

$('.last').on('click', () => {
    let url = window.prompt('è¯·è¾“å…¥ç½‘å€')
    if (url !== null) {//åˆ¤æ–­ç”¨æˆ·è¾“å…¥çš„urlä¸ä¸ºç©º
        if (url.indexOf('http') !== 0) {
            url = 'https://' + url
        }
        hashMap.push({logo: simplifyUrl(url)[0], url: url})
        render()
    }

})
window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap)
    localStorage.setItem('x', string)
}
$(document).on('keypress', (e) => {
    if ($search.is(":focus") === false) {//åˆ¤æ–­æœç´¢æ¡†æœªèŽ·å¾—ç„¦ç‚¹
        const key = e.key
        hashMap.forEach(node => {
            if (node.logo.toLowerCase() === key) {
                window.open(node.url)
            }
        })
    }


})